import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';
import { ContactGroup } from '../../_models';

@Injectable({ providedIn: 'root' })
export class ContactGroupService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllContactGroups() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllContactGroups');
        
        return response;

    }

    saveContactGroup(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveContactGroup',data);
        return response;
  }
   
  updateContactGroup(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateContactGroup',data);
        return response;
  }
  deleteContactGroup(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteContactGroup',data);
        return response;
  }
}