import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../app.config';
import { Graph } from '../_models';

@Injectable({ providedIn: 'root' })
export class GraphService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllGraphs() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllGraphs');
        
        return response;

    }

    saveGraph(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveGraph',data);
        return response;
  }
    
  updateGraph(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateGraph',data);
        return response;
  }
  deleteGraph(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteGraph',data);
        return response;
  }
}