
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../app.config';

@Injectable({ providedIn: 'root' })
export class QuestionService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllQuestions() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllCommunityQuestions');
        
        return response;

    }

    saveQuestion(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveCommunityQuestion',data);
        return response;
  }
  answerQuestion(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'answerCommunityQuestion',data);
        return response;
  }
   
   
  updateQuestion(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateCommuinityQuestion',data);
        return response;
  }
  deleteQuestion(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteCommunityQuestion',data);
        return response;
  }
   
}