import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';
import { Article } from '../../_models';

@Injectable({ providedIn: 'root' })
export class ArticleService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllArticles(data) {
        let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'getAllArticles',data);
        
        return response;

    }
     getAllArticleCategories() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllArticleCategories');
        
        return response;

    }

     getArticleLookups(){
     // let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getArticleLookups');
     //    return response;
         let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllArticleCategories');
        
        return response;
  }
    

    saveArticle(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveArticle',data);
        return response;
  }

   findArticle(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'findArticle',data);
        return response;
  }

 
  updateArticle(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateArticle',data);
        return response;
  }
  publishAction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'publishArticleAction',data);
        return response;
  }
   unpublishAction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'unpublishArticleAction',data);
        return response;
  }
   reviewedAction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'reviewedAction',data);
        return response;
  }
  deleteArticle(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteArticle',data);
        return response;
  }
}