import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';
import { UserGroup } from '../../_models';

@Injectable({ providedIn: 'root' })
export class UserGroupService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllUserGroups() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllUserGroups');
        
        return response;

    }

   getThisUserGroupPermissions(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'getThisUserGroupPermissions',data);
        return response;
  }


    saveUserGroup(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveUserGroup',data);
        return response;
  }
    saveUserGroupPermission(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveUserGroupPermissions',data);
        return response;
  }
  updateUserGroup(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateUserGroup',data);
        return response;
  }
  deleteUserGroup(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteUserGroup',data);
        return response;
  }
}