
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../app.config';

@Injectable({ providedIn: 'root' })
export class LookupService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
    currentUserToken = this.appConfig.currentUserToken;
    // product category begins
    saveClientLogo(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveClientLogo',data);
        return response;
  }
  

    getConfiguration() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getConfiguration');
        
        return response;
    }
    updateConfiguration(data){
        let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateConfiguration',data);
        return response;
    }

    updateAboutus(data){
        let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateConfigurationAboutus',data);
        return response;
    }
     updateContactus(data){
        let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateConfigurationContactus',data);
        return response;
    }
   
    updatePolicy(data){
        let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateConfigurationPolicy',data);
        return response;
    }
     updateTerms(data){
        let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateConfigurationTerms',data);
        return response;
    }
   getDashboardData(data=""){
        let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'getDashboardData',data);
        return response;
    }
}