import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';
import { ArticleCategory } from '../../_models';

@Injectable({ providedIn: 'root' })
export class ArticleCategoryService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllArticleCategories() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllArticleCategories');
        
        return response;

    }

    saveArticleCategory(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveArticleCategory',data);
        return response;
  }
   
  updateArticleCategory(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateArticleCategory',data);
        return response;
  }
  deleteArticleCategory(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteArticleCategory',data);
        return response;
  }
}