﻿import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { User } from '../_models';
import { AppConfig } from '../app.config';
import { Profile } from '../_models';

@Injectable({ providedIn: 'root' })
export class AuthenticationService {
    private currentUserSubject: BehaviorSubject<User>;
    public currentUser: Observable<User>;
    roles = [];
    result = [];
    pageTags = [];
    pages = [];
    menuItems = [];
    functionItems = [];
    functions = [];

    constructor(private http: HttpClient,public appConfig: AppConfig) {
        this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('currentUser')));
        this.currentUser = this.currentUserSubject.asObservable();
    }

    public get currentUserValue(): User {
        return this.currentUserSubject.value;
    }

    login(username: string, password: string) {
        return this.http.post<any>(this.appConfig.api_base_url+'user/authenticate', { email: username, password:password })
            .pipe(map(user => {
                // login successful if there's a jwt token in the response
                if (user && user.token) {
                    // store user details and jwt token in local storage to keep user logged in between page refreshes
                    // store user details and jwt token in local storage to keep user logged in between page refreshes
                    this.pageTags = [];
                    this.pages = [];
                    this.menuItems = [];
                    this.functionItems = [];
                    this.functions = [];
                    this.roles = [];
                    this.result = user;
                    // loop through user permission and prepare list of pages groups, pages functions
                    user.permissions.forEach((record) => {
                        // prepare and setup page group list
                        if(record.page_id != null ){
                        let page_tag_data = {p_tag_name : record.tag, p_id: record.page_id };
                        // check to see if page group does not exist b4 u push
                        let foundTag = this.pageTags.some(function (el) {
                            return el.p_tag_name === record.tag;
                        });
                        if (!foundTag) { this.pageTags.push(page_tag_data); }

                        //prepare and setup pages list
                        let page_data = {p_name : record.page_name, p_id: record.page_id,p_read: record.read_link,p_tag:record.tag,icon: record.icon };
                        // check to see if page does not exist b4 u push
                        let foundPage = this.pages.some(function (el) {
                            return el.p_id === record.page_id;
                        });
                        if (!foundPage) { this.pages.push(page_data); this.menuItems.push(record.read_link)}
                        
                        // prepare and setup function list
                        let page_function_data = {f_name : record.function_name, f_id: record.page_function_id,page:record.page,p_link: record.post_link };
                         // check to see if function does not exist b4 u push
                        let foundFunction = this.functions.some(function (el) {
                            return el.f_id === record.page_function_id;
                        });
                        if (!foundFunction) { this.functions.push(page_function_data); this.functionItems.push(record.post_link)}
                        
                    }
                    });

                    // conbine into roles
                    let permissions = {pageTags:this.pageTags,pages: this.pages,functions: this.functions,menuItems:this.menuItems,functionsItems:this.functionItems}
                    this.roles.push(permissions);

                    let currentUserData = {token:user.token, roles: this.roles,user_info:user.result}
                    localStorage.setItem('currentUser', JSON.stringify(currentUserData));

                    this.appConfig.currentUser = currentUserData; // set the user object
                    // this.appConfig.currentUser = JSON.parse(JSON.stringify(currentUserData)); // set the user object
                    this.appConfig.currentUserInfo = new Profile(user.result); // set the user object
                    // localStorage.setItem('currentUser', JSON.stringify(user));
                    this.currentUserSubject.next(user);
                }

                return user;
            }));
    }

    logout() {
        // remove user from local storage to log user out
        // alert('am here');
        localStorage.removeItem('currentUser');
        // this.currentUserSubject.next(null);
    }
}