import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';
import { Page } from '../../_models';

@Injectable({ providedIn: 'root' })
export class PageService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllPages() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllPages');
        
        return response;

    }

    savePage(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'savePage',data);
        return response;
  }

  updatePage(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updatePage',data);
        return response;
  }
  deletePage(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deletePage',data);
        return response;
  }
}