import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';
import { ProductCategory } from '../../_models';

@Injectable({ providedIn: 'root' })
export class ProductCategoryService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllProductCategories() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllProductCategories');
        
        return response;

    }

    saveProductCategory(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveProductCategory',data);
        return response;
  }
   
  updateProductCategory(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateProductCategory',data);
        return response;
  }
  deleteProductCategory(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteProductCategory',data);
        return response;
  }
}