﻿import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { ToastrManager } from 'ng6-toastr-notifications';
import { NgProgress } from '@ngx-progressbar/core';
import { NgProgressRef } from '@ngx-progressbar/core';
import { LookupService } from '../_services/';
import { AppConfig } from '../app.config';


import { AlertService, AuthenticationService } from '../_services';

@Component({templateUrl: 'login.component.html'})
export class LoginComponent implements OnInit {
    loginForm: FormGroup;
    loading = false;
    submitted = false;
    returnUrl: string;
    companyName: string;
    companyLogo: string;
    progress: NgProgressRef;
    costOfMyCoffeeCup:number;

    constructor(
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        public appConfig: AppConfig,
        private LookupService: LookupService,
        private router: Router,
        private authenticationService: AuthenticationService,
        private alertService: AlertService,
        public toastr: ToastrManager,
        public ngProgress: NgProgress
    ) {
        this.costOfMyCoffeeCup = 6;
        // redirect to home if already logged in
        // if (this.authenticationService.currentUserValue) { 
        //     this.router.navigate(['/']);
        // }
    }

    ngOnInit() {
      this.LookupService.getConfiguration().subscribe(configuration => { 
            // this.progress.complete();
            if(configuration.status == "success"){// results is found
                // console.log(configuration.result)
                this.companyName = configuration.result.company_name;
                // console.log(this.companyName);
                localStorage.setItem("client_config", JSON.stringify(configuration.result));
                localStorage.setItem("pagination_length", configuration.result.pagination_length);
                this.appConfig.paginationLength = configuration.result.pagination_length;
                }

            });
        this.progress = this.ngProgress.ref();
        this.loginForm = this.formBuilder.group({
            email: ['', Validators.required],
            password: ['', Validators.required]
        });

// reset login status
        this.authenticationService.logout();

        // get return url from route parameters or default to '/'
        this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    }

    // convenience getter for easy access to form fields
    get f() { return this.loginForm.controls; }

    onSubmit() {
        this.submitted = true;

        // stop here if form is invalid
        if (this.loginForm.invalid) {
            return;
        }

        this.loading = true;
         this.progress.start();
        this.authenticationService.login(this.f.email.value, this.f.password.value)
            .pipe(first())
            .subscribe(
                data => {
                    this.router.navigate([this.returnUrl]);
                },
                error => {
                    this.alertService.error(error);
                    // this.toastr.errorToastr(error, 'Oops!');
                    this.loading = false;
                     this.progress.complete();
                });
    }
}
