import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../app.config';

@Injectable({
  providedIn: 'root'
})
export class ReportService {
  constructor(private http: HttpClient,public appConfig: AppConfig) { }
  // currentUser = JSON.parse(localStorage.getItem("currentUser"));
  currentUserToken = this.appConfig.currentUserToken;
  getAllReports() {
      let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllReports');

      return response;
  }

  getReport(data) {
    let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'getReport',data);

    return response;
  }

  runReport(data) {
    let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'runReport',data);
      return response;
  }

  runReportWithRange(data) {
    let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'runReportDate',data);
      return response;
  }

  saveReport(data){
      let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveReport',data);
      return response;
  }

  updateReport(data){
      let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateReport',data);
      return response;
  }
  deleteReport(data){
      let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteReport',data);
      return response;
  }
  runAdvanceSearch(data){
      let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'runAdvanceSearch',data);
      return response;
  }
  getDeletedSales(data){
      let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'getDeletedSales',data);
      return response;
  }
    getDetailedReports(data){
    let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'getConsolidatedDataReport',data);

    return response;
  }
    searchDataByString(data){
    let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'searchDataByString',data);

    return response;
  }
  
  getStringSearchData(){
    let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getStringSearchData');

    return response;
  }
}
