import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';
import { Page } from '../../_models';

@Injectable({ providedIn: 'root' })
export class PageFunctionService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllPageFunctions() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllPageFunctions');
        
        return response;

    }
    getAllPageFunctionsJoinOthers() {
        let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getAllPageFunctionsJoinOthers');
        
        return response;

    }

    savePageFunction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'savePageFunction',data);
        return response;
  }

  updatePageFunction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updatePageFunction',data);
        return response;
  }
  deletePageFunction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deletePageFunction',data);
        return response;
  }
}