import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { AppConfig } from '../../app.config';
import { Profile } from '../../_models';

declare interface RouteInfo {
    path: string;
    title: string;
    icon: string;
    class: string;
}
export const ROUTES: RouteInfo[] = [
    // { path: 'page-group', title: 'Dashboard',  icon: 'design_app', class: '' },
    { path: '/dashboard', title: 'Dashboard',  icon: 'design_app', class: '' },
    { path: '/icons', title: 'Icons',  icon:'education_atom', class: '' },
    { path: '/maps', title: 'Maps',  icon:'location_map-big', class: '' },
    { path: '/notifications', title: 'Notifications',  icon:'ui-1_bell-53', class: '' },

    { path: '/user-profile', title: 'User Profile',  icon:'users_single-02', class: '' },
    { path: '/table-list', title: 'Table List',  icon:'design_bullet-list-67', class: '' },
    { path: '/typography', title: 'Typography',  icon:'text_caps-small', class: '' }
];

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  menuItems: any[];
  currentUser = null;
  imageUrl = '../../assets/img/user.png';

  constructor(public appConfig: AppConfig) { }

  ngOnInit() {
   
    this.currentUser = JSON.parse(localStorage.getItem("currentUser"));
    // console.log(this.currentUser);
  // currentUser = JSON.parse(localStorage.getItem("currentUser"));

    this.appConfig.currentUserInfo = new Profile(this.currentUser.user_info); // set the user object
    if(this.appConfig.currentUserInfo.profile_image != null){
    this.imageUrl = this.appConfig.currentUserInfo.profile_image;
      
    }

  }
  

  
}
