import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';
import { Contact } from '../../_models';

@Injectable({ providedIn: 'root' })
export class ContactService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    getAllContacts(data="") {
        let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'getAllContacts',data);
        
        return response;

    }

    saveContact(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'saveContact',data);
        return response;
  }
   
  updateContact(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateContact',data);
        return response;
  }
  updateContactDetail(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateContactDetail',data);
        return response;
  }
  deleteContact(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteContact',data);
        return response;
  }

  deleteContactDetail(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteContactDetail',data);
        return response;
  }

  getContactLookups(data=""){
     // let response = this.http.get<any>(this.appConfig.api_base_url_backoffice+'getContactLookups');
     //    return response;
         let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'getContactLookups',data);
        
        return response;
  }
  findContact(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'findContact',data);
        return response;
  }

   publishAction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'publishContact',data);
        return response;
  }
   unpublishAction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'unpublishContact',data);
        return response;
  }
   reviewedAction(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'reviewedAction',data);
        return response;
  }
}