import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../app.config';
import { DocFile } from '../_models';

@Injectable({ providedIn: 'root' })
export class FileService {
    constructor(private http: HttpClient,public appConfig: AppConfig) { }
    // currentUser = JSON.parse(localStorage.getItem("currentUser"));
     currentUserToken = this.appConfig.currentUserToken;
    
    
  updateArticleFile(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'updateArticleFile',data);
        return response;
  }
  deleteArticleFile(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteArticleFile',data);
        return response;
  }
  addProductFile(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'addProductFile',data);
        return response;
  }
  deleteProductFile(data){
     let response = this.http.post<any>(this.appConfig.api_base_url_backoffice+'deleteProductFile',data);
        return response;
  }
}